/*------------------------------------*\
    #LINKS
\*------------------------------------*/

/**
 * Links
 */
a {
	text-decoration: none;
	outline: 0;
	font-size: inherit;
	line-height: inherit;
	color: @color-dark-blue; // actual color
	@media all and (min-width: @bp-med) {
		font-size: inherit;
	}
	&:hover,
	&:focus {
		text-decoration: underline;
		color: @color-dark-blue;
		outline: -webkit-focus-ring-color auto 1px;
	}
	&:visited {
		color: #804108;
	}
	&.btn {
		&:hover,
		&:focus,
		&:active {
			color: @color-white;
			outline: -webkit-focus-ring-color auto 1px;
		}
	}
}
