.form-check {
	position: relative;
	display: block;
	padding-left: 1.25rem;
}
.form-check-input {
	position: absolute;
	margin-top: 0.3rem;
	margin-left: -1.25rem;
}
.form-control,
.cmp-form-text__text {
	display: block;
	width: 100%;
	padding: @gutter;
	line-height: 1.5;
	color: #495057;
	background-color: @color-white;
	background-clip: padding-box;
	border: 1px solid #707070;
	border-radius: 0px !important;
	transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
	margin-bottom: 10px;
}
.form__content {
	padding: 0 0;
	p {
		max-width: 1000px;
	}
}
.cmp-form {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: @gutter 0;
	.form--group {
		width: 100%;
		margin-bottom: 1rem;
		display: inline-block;
		@media all and (min-width: @bp-med) {
			width: 48%;
		}
		label {
			font-weight: normal;
			margin-bottom: @gutter;
		}
	}
	.form--group-lg {
		width: 100%;
	}
	.form--btn {
		display: flex;
		flex-direction: column;
		@media all and (min-width: @bp-med) {
			display: flex;
			flex-direction: row;
			align-items: center;
		}
		button {
			padding: 18px 90px;
			margin-right: 0;
			margin-bottom: @gutter;
			@media all and (min-width: @bp-med) {
				margin-right: @gutter;
			}
		}
		p {
			width: 100%;
			@media all and (min-width: @bp-med) {
				width: 175px;
			}
		}
	}
	.character--count {
		margin-top: @gutter;
		span {
			display: inline-block;
			color: @color-light-blue;
		}
	}
}
sup {
	color: @color-dark-red;
	margin-left: 5px;
}
.form-check-inline {
	display: block;
	align-items: center;
	padding-left: 0;
	margin-right: 0.75rem;
	@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
		display: block;
	}
	@media all and (min-width: @bp-large) {
		display: inline-flex;
	}
	label {
		margin-right: @gutter;
		display: inline-block;
	}
}
.legend {
	width: 100%;
	font-size: 1.25rem;
	margin-top: @gutter-double;
	@media all and (min-width: @bp-med) {
		font-size: 1.5rem;
	}
}
.form__elements--group {
	width: 100%;
	display: block;
	overflow: hidden;
}

.btn--cancel {
	display: flex !important;
	justify-content: flex-start;
	align-items: flex-start;
	flex-direction: column;
	@media all and (min-width: @bp-med) {
		flex-direction: row;
	}
	p {
		margin-left: 0;
		margin-top: 15px;
		@media all and (min-width: @bp-med) {
			margin-left: 15px;
			margin-top: 0;
		}
	}
}
.cmp-form button.cmp-form-button {
	width: 100%;
	padding: 18px 70px !important;
	@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
		width: auto;
	}
	@media all and (min-width: @bp-large) {
		width: 236px;
	}
}
