.apply__content {
	padding: @gutter-double 0 @gutter 0;
	text-align: center;
	color: @color-white;
	background-color: @color-light-green;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	margin-top: @gutter-double;
	margin-bottom: @gutter;
	p {
		color: @color-white;
	}
	.offsetB15 {
		.fa-desktop,
		.fa-phone,
		.fa-user {
			font-size: 2.25rem;
			vertical-align: middle;
		}
	}
	label {
		position: relative;
		input {
			margin-top: @gutter;
			padding-right: 30px;
		}
		span {
			position: absolute;
			bottom: 26%;
			right: 3%;
			color: grey;
			@media only screen and (min-width: @bp-med) {
				right: 2%;
			}
		}
	}
}
