@charset "UTF-8";

// Default Variables
@slick-arrow-color: white;
@slick-dot-color: black;
@slick-dot-color-active: @slick-dot-color;
@slick-dot-character: "\f192";
@slick-dot-size: 1rem;
@slick-opacity-default: 0.75;
@slick-opacity-on-hover: 1;
@slick-opacity-not-active: 0.25;

/* Arrows */
.slick-prev {
	left: -20px;
	position: absolute;
	display: block;
	height: 38px;
	width: 38px;
	line-height: 0px;
	font-size: 0px;
	cursor: pointer;
	background: transparent;
	top: 50%;
	-webkit-transform: translate(0, -50%);
	-ms-transform: translate(0, -50%);
	transform: translate(0, -50%);
	padding: 6px;
	text-align: center;
	z-index: 9;
	outline: none;
	color: @color-dark-blue;
	&:before {
		font-size: 20px;
		line-height: 1;
		color: @slick-arrow-color;
		opacity: @slick-opacity-default;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		font-weight: normal;
		font-style: normal;
	}
	&:hover,
	&:focus {
		outline: none;
		background: transparent;
		color: @color-blue-lighter;
		&:before {
			opacity: @slick-opacity-on-hover;
		}
	}
	&.slick-disabled:before {
		opacity: @slick-opacity-not-active;
	}
}

.slick-next {
	right: -20px;
	position: absolute;
	display: block;
	height: 38px;
	width: 38px;
	line-height: 0px;
	font-size: 0px;
	cursor: pointer;
	background: transparent;
	top: 50%;
	-webkit-transform: translate(0, -50%);
	-ms-transform: translate(0, -50%);
	transform: translate(0, -50%);
	padding: 6px;
	text-align: center;
	z-index: 9;
	outline: none;
	color: @color-dark-blue;
	&:before {
		font-size: 20px;
		line-height: 1;
		color: @slick-arrow-color;
		opacity: @slick-opacity-default;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		font-weight: normal;
		font-style: normal;
	}
	&:hover,
	&:focus {
		outline: none;
		background: transparent;
		color: @color-blue-lighter;
		&:before {
			opacity: @slick-opacity-on-hover;
		}
	}
	&.slick-disabled:before {
		opacity: @slick-opacity-not-active;
	}
}

/* Dots */

.slick-dotted .slick-slider {
	margin-bottom: 30px;
}

.slick-dots {
	position: absolute;
	bottom: -25px;
	list-style: none;
	display: block;
	text-align: center;
	padding: 0;
	margin: 0;
	width: 100%;
	li {
		position: relative;
		display: inline-block;
		height: 20px;
		width: 20px;
		margin: 0 5px;
		padding: 0;
		cursor: pointer;
		button {
			border: 0;
			background: transparent;
			display: block;
			height: 20px;
			width: 20px;
			outline: none;
			line-height: 0px;
			font-size: 0px;
			color: transparent;
			padding: 5px;
			box-shadow: none;
			cursor: pointer;
			&:hover,
			&:focus {
				outline: none;
				&:before {
					opacity: @slick-opacity-on-hover;
				}
			}
			&:before {
				position: absolute;
				top: 0;
				left: 0;
				content: @slick-dot-character;
				width: 20px;
				height: 20px;
				font-family: 'FontAwesome';
				font-size: @slick-dot-size;
				line-height: 20px;
				text-align: center;
				color: @color-gray-65;
				opacity: @slick-opacity-not-active;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}
		&.slick-active button:before {
			color: @slick-dot-color-active;
			opacity: @slick-opacity-default;
		}
	}
}
