@color--more-text: @color-dark-blue;

.rte__content {
	margin-top: @gutter-double;
	ul,
	ol,
	dl {
		padding: 0 0 0 @gutter;
		li,
		dt,
		dd {
			color: @color-black;
			margin: 10px 0;
		}
	}
	ul {
		list-style: disc;
	}
	ol {
		list-style: decimal;
	}
	a {
		color: @color-blue;
	}
	p a {
		display: inline-block;
		text-decoration: underline;
		color: @color-dark-blue;
	}
	&#responsivegrid-container-text {
		a {
			color: @color-white !important;
		}
	}
}

.showmore__border--btn {
	align-items: center;
	display: flex;
	padding-top: @gutter;
	small {
		font-size: inherit;
	}
	span {
		background: @color--more-text;
		display: block;
		height: 1px;
		width: 100%;
	}

	button {
		align-items: center;
		background: none;
		color: @color--more-text;
		display: flex;
		font: @font-weight-regular @font-size-med-3 @font;
		padding: 0 10px;
		box-shadow: none;
		width: 200px;
		i {
			font-size: @font-size-small-3;
			padding-left: 5px;
			&.fa-chevron-up {
				&:before {
					content: '\F077' !important;
				}
			}
		}
	}
}
.prgms-srvs-left-list-item {
	.heading--large {
		a {
			font-size: 1.3125rem;
			@media all and (min-width: @bp-med) {
				font-size: 2.25rem;
			}
		}
	}
}
.prgms__srvs-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 0.9375rem 0;
	&.gutterLR15 {
		padding: 0 !important;
	}
	.prgms-srvs-left-list-item {
		margin-top: 1.875rem;
		width: 100%;
		margin-bottom: 1rem;
		@media all and (min-width: @bp-med) {
			width: 48%;
		}
	}
}

.news__archive,
.news__archive-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 0.9375rem 0;
	.title__img__list {
		margin-top: 1.875rem;
		width: 100%;
		margin-bottom: 1rem;
		@media all and (min-width: @bp-med) {
			width: 45%;
		}
		.heading--large {
			font-size: 1.3125rem;
			@media all and (min-width: @bp-med) {
				font-size: 2.25rem;
			}
		}
	}
}
.cmp-image__link {
	margin: 0;
	display: block;
}
.cmp-image__image {
	margin: @gutter 0;
}
a.cmp-image__link {
	margin: @gutter 0;
	display: block;
	.cmp-image__link {
		margin: 0;
		display: block;
	}
}
