.title__img__list {
	margin: @gutter-double 0 50px 0;
	img {
		width: 100%;
	}
	.heading--xsmall {
		font-weight: @font-weight-bold;
	}
}
.banner--button {
	padding: 0 !important;

	.fa-caret-right {
		display: none;
	}
	button {
		font-size: @font-size-med-3;
		padding: @gutter;
		color: @color-white;
		width: 100%;

		span {
			color: #2a3991;
			background: @color-white;
			border-radius: 2px;
			padding: 4px 10px;
			margin-right: @gutter;
		}
		small {
			font-size: inherit;
		}
	}
	&.disabled {
		button {
			background: rgba(35, 30, 32, 0.2);
			cursor: text;
			span {
				color: @color-white;
				background: transparent;
			}
		}
	}
}

.card--button {
	padding: 0 !important;

	&.right--icon-none {
		.fa-caret-right {
			display: none;
		}
	}
	button,
	a {
		background: #ededed;
		display: flex;
		flex-grow: 1;
		min-height: 50px;
		margin: 0.9375rem 0;
		padding: 0;
		box-shadow: none;
		width: 100%;
		align-items: center;
		border: none;
		&:hover {
			background: #ededed;
			small {
				color: @color-dark-blue;
				font-weight: @font-weight-bold;
			}
		}

		.list__view--icon {
			width: 40px;
			align-items: center;
			display: flex;
			justify-content: center;
			margin: 0 0.9375rem 0 0;
			color: @color-white;
			height: 50px;
			min-height: 50px;
		}

		.list__view--iconText {
			font-size: 12px;
			font-weight: @font-weight-bold;
			font-style: italic;
		}
		small {
			display: flex;
			align-items: center;
			vertical-align: middle;
			width: 100%;
			color: @color-black;
			font-size: 1.125rem;
			text-align: left;
		}
		.fa-caret-right {
			background: transparent;
			color: grey;
		}
	}
}
.transparent--button {
	padding: 0 !important;

	.fa-caret-right {
		display: none;
	}
	button {
		height: 70px;
		margin: @gutter-double 0;
		text-align: left;
		border: 1px solid @color-white;
		border-radius: 4px;
		background: transparent;
		color: @color-white;
		font-size: 1.25rem;
		width: 350px;
		box-shadow: none;
		&:hover {
			background: transparent;
			border: 1px solid rgba(255, 255, 255, 0.5);
		}
		span {
			margin-right: @gutter;
		}
		small {
			font-size: inherit;
		}
	}

	&.icon__bg--orange {
		span {
			color: #695621 !important;
			background: transparent;
		}
	}
	&.icon__bg--red {
		span {
			color: @color-dark-red !important;
			background: transparent;
		}
	}
	&.icon__bg--blue {
		span {
			color: @color-light-blue !important;
			background: transparent;
		}
	}
	&.icon__bg--dark-blue {
		span {
			background: @color-dark-blue;
			background: transparent;
		}
	}
	&.icon__bg--light-blue {
		span {
			color: @color-blue-lighter !important;
			background: transparent;
		}
	}
	&.icon__bg--lighter-blue {
		span {
			color: #6cbee5 !important;
			background: transparent;
		}
	}
	&.icon__bg--green {
		span {
			color: @color-green !important;
			background: transparent;
		}
	}
	&.icon__bg--purple {
		span {
			color: #1a6387 !important;
			background: transparent;
		}
	}
}
.font-1x .fa {
	font-size: 1rem;
}
.font-2x .fa {
	font-size: 2rem;
}
.font-3x .fa {
	font-size: 3rem;
}
.font-4x .fa {
	font-size: 4rem;
}
.font-5x .fa {
	font-size: 5rem;
}
