@baseClass: .accordion-card;
.faq__accordion {
	.faq-accordion__content {
		display: block;
	}
	.faq__pattern {
		position: absolute;
		height: 100%;
		width: 39px;
		left: 0;
		top: 0;
		@media only screen and (min-width: @bp-med) {
			width: 40px;
		}
	}
	.accordion-content__head {
		padding: 0 15px 15px 0px;
		display: flex;
		align-items: center;
		vertical-align: middle;
		width: 100%;
		height: 100%;
		.rte__content {
			margin-top: 0px;
			p {
				a {
					color: @color-blue;
					display: inline-block;
					margin: 0 3px;
				}
			}
		}
	}
	.accordion-card {
		&__header {
			background: #ededed;
			padding: 0px;
			border: none;
			&--icon {
				padding-right: @gutter;
				button {
					background: none;
					border: none;
					padding: 0;
					padding-left: 5px;

					i {
						color: grey;
						font-size: @font-size-med-3;
						&.fa-plus-square {
							display: none;
						}
					}
				}
			}
			&--iconLeft {
				padding: @gutter;
				align-items: center;
				justify-content: center;
				margin: 0 0.9375rem 0 0;
				color: @color-white;
			}
			&.active {
				.fa-caret-right:before {
					content: '\F0D8';
				}
			}
		}

		&__content {
			display: flex;
			position: relative;
			max-height: 0;
			opacity: 1;
			transition-timing-function: ease-in;
			transition-duration: 0.2s;
			background: #ededed;
			.accordion-card__header--iconLeft {
				&:before {
					visibility: hidden;
				}
			}
			.accordion-card__header--icon {
				visibility: hidden;
			}
		}
	}
}
