/**
 * Body base styles
 */
@import '../../../node_modules/@fullcalendar/core/main.css';
@import '../../../node_modules/@fullcalendar/daygrid/main.css';
@import '../../../node_modules/@fullcalendar/timegrid/main.css';
@import '../../../node_modules/@fullcalendar/list/main.css';
body {
  background: #fff;
  color: #000;
  font: 300 1rem/1.2 Arial, Helvetica, sans-serif;
  -webkit-text-size-adjust: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}
@media all and (min-width: 48rem) {
  body {
    font: 300 1.125rem/1.2 Arial, Helvetica, sans-serif;
  }
}
.btn,
.banner--button button,
.banner--button a {
  display: inline-block;
  margin-bottom: 0;
  font-weight: 300;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  padding: 8px 23px;
  font-size: 0.875rem;
  border-radius: 4px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  background-color: #2a3991;
}
.btn span,
.banner--button button span,
.banner--button a span {
  margin-right: 0.9375rem;
  vertical-align: middle;
}
.btn--primary {
  background: #d9d9d9;
}
.btn--large {
  font-size: 1.125rem;
  display: block;
  width: 100%;
  height: 60px;
}
.btn--medium {
  width: 70%;
}
.btn--shadow {
  box-shadow: 0px 3px 6px #000000;
}
.btn--transparent {
  background: transparent;
  color: #000;
}
.btn--cancel button {
  background: #e5e5e6;
  color: #000;
}
.btn--cancel button:hover {
  background: #e5e5e6;
  color: #000;
}
.embed-responsive {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;
}
.embed-responsive:before {
  display: block;
  content: '';
}
.embed-responsive .embed-responsive-item,
.embed-responsive iframe,
.embed-responsive embed,
.embed-responsive object,
.embed-responsive video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
.embed-responsive-21by9:before {
  padding-top: 42.85714286%;
}
.embed-responsive-16by9:before {
  padding-top: 56.25%;
}
.embed-responsive-4by3:before {
  padding-top: 75%;
}
.embed-responsive-1by1:before {
  padding-top: 100%;
}
/*------------------------------------*\
    #FORMS
\*------------------------------------*/
/**
 * Fieldset base styles
 */
fieldset {
  border: 0;
  padding: 0;
  margin: 0;
}
/**
 * Label base styles
 */
label {
  display: block;
  margin-bottom: 0.9375rem !important;
  font-weight: 700;
}
/**
 * Form button, input, select, and text area base styles
 * 1) Zero out margin and inherit font styles
 */
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}
/**
 * Input and text area base styles
 * 1) Width entire container, gray border, padding
 */
input,
textarea {
  width: 100%;
  border: 1px solid #707070;
  padding: 0.625rem;
  margin-bottom: 0.9375rem;
}
/**
 * Input areas base styles
 * 1) Get rid of webkit appearance of these areas
 */
input[type='text'],
input[type='search'],
input[type='url'],
input[type='number'],
textarea {
  -webkit-appearance: none;
  border-radius: 4px;
}
/**
 * Button/Submit button base styles
 */
button,
input[type='submit'] {
  padding: 0.9375rem;
  background: #2a3991;
  border: 1px solid transparent;
  cursor: pointer;
  -webkit-box-shadow: 0px 3px 6px 0px rgba(255, 117, 117, 0.5);
  -moz-box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.5);
  color: #fff;
  margin: 5px 0;
}
button:hover,
input[type='submit']:hover {
  background: #2e40a7;
}
/**
 * Checkboxes/Radio button base styles
 */
input[type='checkbox'],
input[type='radio'] {
  width: auto;
  margin-right: 0.3em;
}
/**
 * Search cancel button and search decoration webkit appearance none
 */
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type='search']::-ms-clear {
  display: none;
}
/** 
*	Set up the height and font of inputs
**/
input {
  color: #676f77;
  font-size: 1rem;
  line-height: 1.3125rem;
}
/** 
*	Set up the styled selectbox
**/
.styled-select,
.styled-select select {
  width: auto;
}
select:focus {
  outline: none;
}
.styled-select select {
  height: auto;
  padding: 5px 0 5px 5px;
  background-color: white;
  border: none;
  font-size: 14px;
  margin-bottom: 0.9375rem;
  line-height: inherit;
  font-weight: 700;
}
@media only screen and (min-width: 48rem) {
  .styled-select select {
    font-size: 2rem;
    margin-bottom: 0.9375rem;
  }
}
/*------------------------------------*\
    #GLOBAL CLASSES
\*------------------------------------*/
/**
 * Clearfix
 */
.cf {
  *zoom: 1;
}
/**
 * Clearfix pseudo-elements
 */
.cf:before,
.cf:after {
  content: ' ';
  /* 1 */
  display: table;
  /* 2 */
}
.cf:after {
  clear: both;
}
/**
 * Completely remove from the flow and screen readers.
 */
.is-hidden {
  display: none !important;
  visibility: hidden !important;
}
/**
 * Cursor Text
 */
.cursor--text button,
.cursor--text a {
  cursor: text;
}
/**
 * Completely remove from the flow but leave available to screen readers.
 */
.is-vishidden {
  position: absolute !important;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}
.b-skip-navigation {
  background: #2a3991;
}
.b-skip-navigation a {
  color: #fff;
  height: 1px;
  left: -10000px;
  overflow: hidden;
  position: absolute;
  top: auto;
  width: 1px;
}
.b-skip-navigation a:focus,
.b-skip-navigation a:visited {
  color: #fff;
  height: auto;
  position: static;
  width: auto;
}
/**
 * Making background image position scroll
 */
.bg__img--attachment-scroll {
  background-attachment: scroll !important;
}
/**
 * Text align right
 */
.align-right {
  text-align: right;
}
/**
 * Text align center
 */
.align-center {
  text-align: center;
}
/**
 * Text Align left
 */
.align-left {
  text-align: left !important;
}
/**
 * Block level element
 */
.el--block {
  display: block;
}
/**
 * Inlin-Block level element
 */
.el--inline-block {
  display: inline-block;
}
/**
 * Inline level element
 */
.el--inline {
  display: inline;
}
/**
 * Hide on small screens
 */
@media all and (max-width: 767px) {
  .hide-small {
    display: none;
  }
}
/**
 * Hide on medium screens
 */
@media all and (min-width: 48rem) and (max-width: 65.4375rem) {
  .hide-med {
    display: none;
  }
}
/**
 * Hide on large screens
 */
@media all and (min-width: 65.4375rem) {
  .hide-large {
    display: none;
  }
}
/**
 * Show valid text color
