/*------------------------------------*\
    #LINKS
\*------------------------------------*/
/**
 * Links
 */
a {
  text-decoration: none;
  outline: 0;
  font-size: inherit;
  line-height: inherit;
  color: #2a3991;
}
@media all and (min-width: 48rem) {
  a {
    font-size: inherit;
  }
}
a:hover,
a:focus {
  text-decoration: underline;
  color: #2a3991;
  outline: -webkit-focus-ring-color auto 1px;
}
a:visited {
  color: #804108;
}
a.btn:hover,
a.btn:focus,
a.btn:active {
  color: #fff;
  outline: -webkit-focus-ring-color auto 1px;
}
/*------------------------------------*\
    #LINKS
\*------------------------------------*/
/**
 * Ordered/Unordered list base styles
 */
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
li {
  font-size: 1rem;
  color: #fff;
}
@media all and (min-width: 48rem) {
  li {
    font-size: 1.125rem;
  }
}
/**
 * Definition list base styles
 */
dl {
  overflow: hidden;
  margin: 0 0 0.9375rem;
}
/**
 * Terms/Names base styles
 */
dt {
  font-weight: 700;
}
/**
 * Definition list description base styles
 */
dd {
  margin-left: 0;
}
/*------------------------------------*\
    #MAIN
\*------------------------------------*/
/**
 * Main content area
 */
[role=main] {
  flex: 1;
}
/*------------------------------------*\
    #MEDIA ELEMENTS
\*------------------------------------*/
/**
 * Make media elements responsive
 */
img,
video,
object {
  max-width: 100%;
  height: auto;
}
/**
 * Iframe base styles
 */
iframe {
  margin-bottom: 0.9375rem;
}
/**
 * Figure base styles
 */
figure {
  margin-bottom: 0.9375rem;
  /**
	 * Figure image base styles
	 */
}
figure img {
  margin-bottom: 0.9375rem;
}
/**
 * Figure caption base styles
 */
figcaption {
  font-style: italic;
}
/*------------------------------------*\
    #RESET
\*------------------------------------*/
/**
 * Use border box on all elements
 */
* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
/**
 * Zero out margin and padding on most elements as base style
 */
html,
body,
div,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
ol,
ul,
li,
form,
legend,
label,
table,
header,
footer,
nav,
section,
figure {
  margin: 0;
  padding: 0;
}
/**
 * Display block as base for these elements
 */
header,
footer,
nav,
section,
article,
hgroup,
figure {
  display: block;
}
/*------------------------------------*\
    #TABLE
\*------------------------------------*/
/**
 * Table base styles
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}
/**
 * Table header base styles
 */
th {
  text-align: left;
  padding: 0.2em;
}
/**
 * Table cell base styles
 */
td {
  padding: 0.2em;
}
/*------------------------------------*\
    #TEXT
\*------------------------------------*/
/**
 * Paragraph base styles
 */
p {
  margin-bottom: 0.9375rem;
  font-size: 1rem;
  color: #000;
  line-height: 1.65rem;
}
@media all and (min-width: 48rem) {
  p {
    font-size: 1.125rem;
  }
}
/**
 * Blockquote base styles
 */
blockquote {
  font-style: italic;
  border-left: 1px solid #808080;
  color: #808080;
  padding-left: 0.9375rem;
  margin-bottom: 0.9375rem;
}
/**
 * Horizontal rule base styles
 */
hr {
  border: 0;
  height: 2px;
  background: #d9d9d9;
  margin: 1.875rem 0;
}
/**
 * Abbreviation base styles
 */
abbr {
  border-bottom: 1px dotted #808080;
  cursor: help;
}
