/* centered fluid container, used for full width components */
.container {
  width: 100%;
  max-width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media only screen and (min-width: 48rem) and (max-width: 65.375rem) {
  .container {
    max-width: 48rem;
    width: 100%;
  }
}
@media all and (min-width: 65.4375rem) {
  .container {
    max-width: 75rem;
    width: 100%;
  }
}
/* fixed width centered container, used for fixed width components */
/* max-width can also be set per breakpoint as needed */
.main-content {
  width: 950px;
  margin: 0 auto;
}
.cmp-container .container {
  padding: 0;
}
.xf-content-height {
  margin: 0 !important;
}
@media all and (max-width: 48rem) {
