/*------------------------------------*\
    $TINGLE
\*------------------------------------*/
/* ----------------------------------------------------------- */
/* == tingle v0.15.1 */
/* ----------------------------------------------------------- */
.tingle-modal * {
  box-sizing: border-box;
}
.tingle-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: -ms-flexbox;
  display: flex;
  visibility: hidden;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
}
.tingle-modal-box {
  position: relative;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  margin-top: auto;
  margin-bottom: auto;
  width: 80%;
  border-radius: 4px;
  background: #fff;
  opacity: 1;
  cursor: auto;
  will-change: transform, opacity;
}
@media only screen and (min-width: 48rem) and (max-width: 65.375rem) {
  .tingle-modal-box {
    width: 60%;
  }
}
@media only screen and (min-width: 65.4375rem) {
  .tingle-modal-box {
    width: 35%;
  }
}
/* confirm and alerts
  -------------------------------------------------------------- */
.tingle-modal--confirm .tingle-modal-box {
  text-align: center;
}
/* modal
  -------------------------------------------------------------- */
.tingle-modal__close {
  position: absolute;
  top: 0;
  right: 1rem;
  z-index: 1000;
  padding: 5px;
  width: 1rem;
  height: 1rem;
  border: none;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  box-shadow: none;
  display: none;
}
.tingle-modal__close:hover {
  background: transparent;
}
.tingle-modal-box__footer .tingle-modal__close {
  display: block;
}
.tingle-modal--noOverlayClose {
  cursor: default;
}
.tingle-modal--noClose .tingle-modal__close {
  display: none;
}
.tingle-modal__close svg * {
  fill: currentColor;
}
.tingle-modal__closeLabel {
  display: none;
}
.tingle-modal__close:hover {
  color: #fff;
}
.tingle-modal-box__content {
  padding: 3rem 3rem;
}
/* state
  -------------------------------------------------------------- */
.tingle-enabled {
  position: fixed;
  right: 0;
  left: 0;
  overflow: hidden;
}
.tingle-enabled .tingle-content-wrapper {
  filter: blur(8px);
}
.tingle-modal--visible {
  visibility: visible;
  opacity: 1;
}
.tingle-modal--visible .tingle-modal-box {
  animation: scale 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) forwards;
}
.tingle-modal--overflow {
  overflow-y: scroll;
  padding-top: 8vh;
}
/* btn
  -------------------------------------------------------------- */
.tingle-btn {
  display: inline-block;
  margin: 0 0.5rem;
  padding: 1rem 2rem;
  border: none;
  background-color: grey;
  box-shadow: none;
  color: #fff;
  vertical-align: middle;
  text-decoration: none;
  font-size: inherit;
  font-family: inherit;
  line-height: normal;
  cursor: pointer;
  transition: background-color 0.4s ease;
}
.tingle-btn--primary {
  background-color: #3498db;
}
.tingle-btn--danger {
  background-color: #e74c3c;
}
.tingle-btn--default {
  background-color: #34495e;
}
.tingle-btn--pull-left {
  float: left;
}
.tingle-btn--pull-right {
  float: right;
}
/* responsive
  -------------------------------------------------------------- */
@media (max-width: 540px) {
  .tingle-modal {
    top: 0px;
    display: block;
    padding-top: 60px;
    width: 100%;
  }
  .tingle-modal-box {
    width: auto;
    border-radius: 0;
  }
  .tingle-modal-box__content {
    overflow-y: scroll;
  }
  .tingle-modal--noClose {
    top: 0;
  }
  .tingle-modal--noOverlayClose {
    padding-top: 0;
  }
}
@supports (
	(-webkit-backdrop-filter: blur(12px)) or (backdrop-filter: blur(12px))
) {
  .tingle-modal {
    -webkit-backdrop-filter: blur(0);
    backdrop-filter: blur(0);
  }
  @media (max-width: 540px) {
    .tingle-modal {
      -webkit-backdrop-filter: blur(0);
      backdrop-filter: blur(0);
    }
  }
  .tingle-enabled .tingle-content-wrapper {
    filter: none;
  }
}
/* animations
  -------------------------------------------------------------- */
@keyframes scale {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
