.alert {
	padding: 10px 0px 5px 0px;
	border: 1px solid transparent;
	display: block;
	justify-content: space-between;
	position: relative;
	align-items: center;
	@media only screen and (min-width: @bp-med) {
		display: flex;
		padding: 5px 0;
	}
	strong {
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			width: 18%;
		}
		span {
			margin-right: 5px;
		}
	}
	p {
		color: @color-white;
		margin-top: 10px;
		margin-bottom: 0;
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			width: 550px;
			margin-top: 2px;
		}
		@media only screen and (min-width: @bp-large) {
			margin-top: 2px;
		}
		a {
			color: @color-white;
			font-weight: @font-weight-regular;
			text-decoration: underline;
		}
	}
	&.alert-dismissible .close {
		position: absolute;
		top: 0px;
		right: 10px;
		color: inherit;
		text-decoration: none !important;
		float: right;
		font-weight: @font-weight-bold;
		padding: 5px;
		order: 3;
		@media only screen and (min-width: @bp-med) {
			position: relative;
			right: 0px;
		}
	}
}
.alert-info {
	color: @color-black;
	background-color: #ffe200;
	border-color: #ffe200;
	p {
		color: @color-black;
		a {
			color: inherit;
		}
	}
}
.alert-danger {
	color: @color-white;
	background-color: @color-dark-red;
	border-color: @color-dark-red;
	.alert-link {
		color: @color-white;
	}
}
