.header__item {
	&-language {
		position: relative;
		padding: @gutter 0;
		display: block !important;
		border-bottom: none !important;
		@media all and (min-width: @bp-large) {
			display: flex;
		}
	}
	.fa-caret-right {
		margin-top: 5px;
		float: right;
		@media all and (min-width: @bp-large) {
			margin-top: 0;
			float: none;
			margin-left: 5px;
		}
	}
	&-current {
		padding: 0 5px !important;
		&:hover {
			text-decoration: none;
		}
	}
	&-dropdown {
		color: @color-white;
		text-decoration: none;
		padding: 0;
		@media all and (min-width: @bp-large) {
			padding: 0 0 0 @gutter;
		}
		&:hover,
		&:focus,
		&:visited {
			color: @color-white;
			text-decoration: none;
		}
	}
	&-list {
		display: block;
		position: relative;
		min-width: 100%;
		box-shadow: none;
		z-index: 1;
		top: 10px;
		left: inherit;
		background-color: #009cdc;
		padding: 0;
		ul {
			margin-top: 15px;
			margin-bottom: 15px;
		}
		&.hide {
			display: none;
		}
		&.show {
			display: block;
		}
		p {
			color: @color-white;
			text-decoration: underline;
			font-style: italic;
			border-bottom: 1px solid @color-white;
			margin-bottom: 10px;
			padding-bottom: 10px;
			@media all and (min-width: @bp-large) {
				font-style: normal;
				border-bottom: none;
				padding-bottom: 0;
			}
		}
		.language-close-icon {
			display: none;
			@media all and (min-width: @bp-large) {
				display: flex;
				cursor: pointer;
				float: right;
			}
		}
		@media all and (min-width: @bp-large) {
			display: none;
			position: absolute;
			box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
			z-index: 1;
			top: 10%;
			left: -10%;
			background-color: @color-light-blue;
			padding: @gutter;
			min-width: 150px;
		}
		li {
			border-bottom: 1px solid#ffffff80;
			margin-bottom: 10px;
			padding-bottom: 10px;
			@media all and (min-width: @bp-large) {
				margin-bottom: 5px;
				padding-bottom: 5px;
			}
			a {
				color: @color-white;
			}
			&.active {
				a {
					font-weight: @font-weight-bold;
				}
			}
		}
	}
}
.up-icon {
	.fa-caret-right {
		&:before {
			content: '\F0D8';
		}
	}
}
