/*------------------------------------*\
    #FORMS
\*------------------------------------*/

/**
 * Fieldset base styles
 */
fieldset {
	border: 0;
	padding: 0;
	margin: 0;
}

/**
 * Label base styles
 */
label {
	display: block;
	margin-bottom: @gutter !important;
	font-weight: @font-weight-bold;
}

/**
 * Form button, input, select, and text area base styles
 * 1) Zero out margin and inherit font styles
 */
button,
input,
select,
textarea {
	font-family: inherit;
	font-size: 100%;
	margin: 0;
}

/**
 * Input and text area base styles
 * 1) Width entire container, gray border, padding
 */
input,
textarea {
	width: 100%;
	border: 1px solid @color-gray-70;
	padding: 0.625rem;
	margin-bottom: @gutter;
}

/**
 * Input areas base styles
 * 1) Get rid of webkit appearance of these areas
 */
input[type='text'],
input[type='search'],
input[type='url'],
input[type='number'],
textarea {
	-webkit-appearance: none;
	border-radius: @border-radius;
}

/**
 * Button/Submit button base styles
 */
button,
input[type='submit'] {
	padding: @gutter;
	background: @color-dark-blue;
	border: 1px solid transparent;
	cursor: pointer;
	-webkit-box-shadow: 0px 3px 6px 0px rgba(255, 117, 117, 0.5);
	-moz-box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.5);
	box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.5);
	color: @color-white;
	margin: 5px 0;
	&:hover {
		background: @color-dark-blue-hover;
	}
}

/**
 * Checkboxes/Radio button base styles
 */
input[type='checkbox'],
input[type='radio'] {
	width: auto;
	margin-right: 0.3em;
}

/**
 * Search cancel button and search decoration webkit appearance none
 */
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
	-webkit-appearance: none;
}
input[type='search']::-ms-clear {
	display: none;
}
/** 
*	Set up the height and font of inputs
**/
input {
	color: #676f77;
	font-size: 1rem;
	line-height: 1.3125rem;
}

/** 
*	Set up the styled selectbox
**/
.styled-select,
.styled-select select {
	width: auto;
}
select:focus {
	outline: none;
}
.styled-select select {
	height: auto;
	padding: 5px 0 5px 5px;
	background-color: white;
	border: none;
	font-size: 14px;
	margin-bottom: @gutter;
	line-height: inherit;
	font-weight: @font-weight-bold;
	@media only screen and (min-width: @bp-med) {
		font-size: 2rem;
		margin-bottom: @gutter;
	}
}
