.tingle__model {
	display: none;
}
.tingle-modal-box__content {
	background: @color-dark-blue;
	display: flex;
	justify-content: center;
	flex-flow: column;
	color: @color-white;
	align-items: center;
	text-align: center;
	padding: 20px 50px;
	font-size: @font-size-small-3;
	@media all and (min-width: @bp-med) {
		font-size: @font-size-small-2;
	}
	a {
		margin: @gutter 0;
		text-align: left;
		border: 1px solid @color-white;
		color: inherit;
		display: flex;
		min-width: 200px;
		align-items: center;
		font-size: inherit;
		box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
		padding: 10px;
		@media all and (min-width: @bp-med) {
			font-size: 1rem;
		}
		&:hover {
			text-decoration: none;
		}
		span {
			font-size: inherit;
			@media all and (min-width: @bp-med) {
				font-size: 1rem;
			}
		}
	}
}
