@media print {
	.news-article__btn {
		display: none !important;
	}
	.news-article__text {
		display: inline-block !important;
	}
	.news-article__title {
		padding: 0 !important;
		p {
			margin: 0 !important;
		}
	}
	.news-article__text img {
		width: 100% !important;
	}
	.alert {
		display: none !important;
	}
	.header__content,
	.breadcrumb__content,
	.footer__content,
	.find__content {
		display: none !important;
	}
}
