.card__list {
	display: flex;
}
.card__content-carousel {
	background: #231e20;
	padding: @gutter-double 0;
}
.card__item {
	padding: 0 @gutter;
}
.cmp-carousel__content {
	margin: @gutter 0;
	.cmp-teaser__description {
		p {
			color: @color-white;
		}
	}

	a.cmp-image__link {
		margin: 0 !important;
	}

	.cmp-teaser__title,
	.cmp-teaser__description,
	.cmp-teaser__action-container {
		margin-left: 5px;
		padding: 5px;
		background: rgba(42, 57, 145, 0.8);
		font-family: @font;
	}
}
.t-cmp-clean {
	font-size: @font-size-small-3;
	@media only screen and (min-width: @bp-med) {
		font-size: @font-size-small-2;
	}
}
