@import '../../../../../node_modules/@fullcalendar/core/main.css';
@import '../../../../../node_modules/@fullcalendar/daygrid/main.css';
@import '../../../../../node_modules/@fullcalendar/timegrid/main.css';
@import '../../../../../node_modules/@fullcalendar/list/main.css';

.calendar__content {
	margin: @gutter 0;
	position: relative;
	.calendar__title {
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		margin-bottom: @gutter;
		@media only screen and (min-width: @bp-med) {
			flex-direction: column;
		}
		@media only screen and (min-width: @bp-large) {
			flex-direction: row;
		}
	}

	.fc-other-month {
		background-color: #ececec;
	}
	.fc-row table {
		margin-bottom: 0px;
	}
	.fc-day-grid-event .fc-time,
	.fc-day-grid-event .fc-title {
		font-size: @font-size-small-4;
		font-weight: normal;
	}
	.fc-toolbar.fc-header-toolbar {
		margin-bottom: 0px;
		background: @color-dark-blue;
		padding: @gutter;
		color: @color-white;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		@media only screen and (min-width: @bp-med) {
			display: flex;
			flex-direction: row;
			align-items: center;
		}
	}
	.fc-toolbar h2 {
		font-weight: normal;
	}
	.fc-day-header {
		color: rgba(0, 0, 0, 0.6);
		font-weight: normal;
		padding: @gutter 0;
	}
	.fc-today-button,
	.fc-dayGridMonth-button,
	button.disabled,
	.fc-exportButton-button {
		background: transparent !important;
		box-shadow: none;
		border: none !important;
		opacity: 1;
		padding: 3px 10px;
	}
	.fc-day-number {
		color: @color-black !important;
	}
	.fc-event,
	.fc-event-dot {
		background-color: @color-dark-blue;
		text-align: left;
		padding-left: 5px;
		.fc-title {
			color: @color-white;
		}
	}
	.fc-unthemed td.fc-today {
		background: #d7e9f8;
	}
	.fc-listWeek-button.fc-button-active,
	.fc-listWeek-button {
		background: @color-white !important;
		color: @color-black;
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		margin: 0;
		box-shadow: none;
		padding: 3px 10px;
	}
	.fc-dayGridMonth-button {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		margin: 0;
	}
	.fc-today-button {
		margin-right: @gutter;
		padding: 0;
	}
	.fc-scroller {
		height: 100% !important;
	}
	.fc-clear {
		display: none;
	}
	.fc-toolbar .fc-right {
		order: 3;
	}
	.fc-state-default {
		color: @color-white;
	}
	.fc-more-popover {
		width: 280px;
		max-height: 280px;
		overflow-x: hidden;
		.fc-event {
			font-size: @font-size-small-3;
			padding: 5px;
			margin-bottom: 5px;
			@media only screen and (min-width: @bp-med) {
				font-size: @font-size-small-2;
			}
		}
	}
	.fc-more-popover .fc-event-container {
		padding: @gutter;
	}
	.fc-popover .fc-header {
		padding: @gutter;
		font-weight: @font-weight-bold;
	}
	.fc-popover .fc-header .fc-close {
		order: 2;
		font-weight: inherit;
	}
	.fc-exportButton-button {
		margin-right: 20px;
		&:after {
			display: inline-block;
			font: normal normal normal 14px/1 FontAwesome;
			font-size: inherit;
			text-rendering: auto;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			content: '\f073';
			margin-left: 5px;
		}
	}
	.mini__CalendarEventList {
		margin: @gutter 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		li {
			margin-bottom: @gutter;
			width: 100%;
			border: 1px solid @color-black;
			border-radius: 4px;
			background: @color-white;
			@media only screen and (min-width: @bp-med) {
				width: 48%;
			}
			.calendar__events {
				background: transparent;
				border-radius: 0;
			}
		}
	}
}
.search-form__element {
	position: relative;
	input {
		margin-right: @gutter-double;
	}
	span {
		position: absolute;
		right: 3%;
		top: 16%;
		color: grey;
		@media only screen and (min-width: @bp-med) {
			top: 14%;
		}
		@media only screen and (min-width: @bp-large) {
			top: 18%;
		}
	}
}
.fc-prev-button,
.fc-next-button {
	background: transparent;
	border: none;
	box-shadow: none;
	padding: 0;
	line-height: inherit;
	&:hover {
		background: transparent;
	}
}

.fc-next-button span {
	display: inline-block;
	font: normal normal normal 1.875rem / 1 FontAwesome !important;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	&:before {
		content: '\F0DA';
	}
}
.fc-prev-button span {
	display: inline-block;
	font: normal normal normal 1.875rem / 1 FontAwesome !important;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	&:before {
		content: '\F0D9';
	}
}

@media only screen and (max-width: @bp-med) {
	.fc-center {
		margin-bottom: @gutter;
		margin-top: @gutter;
	}
}
.calendar__content table th,
.calendar__content table > tbody > tr:nth-of-type(odd),
.calendar__content table > tbody > tr:nth-of-type(even) {
	background: transparent !important;
}
.fc-button-primary:not(:disabled):active,
.fc-button-primary:not(:disabled).fc-button-active {
	color: @color-white;
	background-color: transparent;
	border-color: transparent;
}
