.footer__content {
	background-color: @color-dark-blue;
	text-align: center;
	padding: @gutter-double 0;
	margin-top: @gutter;
}
.footer__copyRight {
	padding: @gutter @gutter-double 0 @gutter-double;
	@media all and (min-width: @bp-med) {
		padding: @gutter 0 0 0;
	}
	.footer__copyRightText,
	p {
		color: @color-white;
		margin-bottom: 5px;
	}
}
.footer__list,
.social__list {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	flex-wrap: wrap;
	-webkit-justify-content: center;
	justify-content: center;
	padding: @gutter 0 0 0;

	.nav__item {
		padding: 0 @gutter;
	}
	.social__item {
		padding: 0 @gutter 0 0;
	}
	.nav__link {
		color: @color-white;
	}
	.social__item a {
		color: @color-dark-blue;
		background: @color-white;
		border-radius: @border-radius-2;
		padding: 0 5px;
	}
}
