@import './events.less';
@import './calendar.less';

.calendar__events {
	background: @color-white;
	display: flex;
	padding: @gutter;
	border-radius: @border-radius;
	.event__scheduler {
		flex-direction: row;
		padding-right: @gutter;
		border-right: 1px solid @color-gray-70;
		width: 30%;
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			width: 25%;
		}
		@media only screen and (min-width: @bp-large) {
			width: 20%;
		}
		.event__day {
			font-size: 1.9375rem;
			color: @color-red;
			display: block;
		}

		.event__date {
			padding: @gutter 0;
			display: inline;
			color: @color-black;
		}
	}
	.event__details {
		flex-direction: row;
		padding-left: @gutter;
		width: 70%;
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			width: 75%;
		}
		@media only screen and (min-width: @bp-large) {
			width: 80%;
		}
		a {
			text-decoration: none;
		}
		strong {
			display: block;
			margin-top: @gutter-double;
			color: @color-black;
			font-weight: @font-weight-bold;
		}
	}
}
.calendar__content-search {
	display: flex;
	justify-content: space-between;
	background: @color-dark-blue;
	padding: 10px @gutter;
	align-items: flex-start;
	@media only screen and (min-width: @bp-med) {
		align-items: center;
	}
	a {
		color: @color-white;
		margin-bottom: @gutter;
		@media only screen and (min-width: @bp-med) {
			margin-bottom: 0;
		}
		span {
			margin-left: 10px;
		}
	}
	input {
		background: @color-dark-blue;
		border: 1px solid @color-white;
		color: @color-white;
		width: 160px;
		height: 32px;
		font-size: @font-size-small-3;
		&::placeholder {
			color: @color-white;
		}
	}
}
.calendar__items {
	li {
		margin-bottom: @gutter;
	}
}
