/* Include this path Responsive Grid file if using AEM 6.3 */
/* @import "/etc/clientlibs/wcm/foundation/grid/grid_base.less"; */

/* Include this path Responsive Grid file if using AEM 6.4 */
/* @import "/libs/wcm/foundation/clientlibs/grid/grid_base.les"; */

/* Include this path Responsive Grid file if using a local version */
@import 'grid_base.less';

/* The following generates the site specific Grid system using the AEM provided grid_base.less provided above */

@max_col: 12; /* maximum amount of grid cells */
@grid-gutter-width: 0; /* grid column padding */

/* the breakpoints set below can be customized based on the ux and front-end requirements */

/* centered fluid container, used for full width components */
.container {
	width: 100%;
	max-width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
		max-width: @bp-med;
		width: 100%;
	}
	@media all and (min-width: @bp-large) {
		max-width: 75rem;
		width: 100%;
	}
}

/* fixed width centered container, used for fixed width components */
/* max-width can also be set per breakpoint as needed */
.main-content {
	width: 950px;
	margin: 0 auto;
}
.cmp-container {
	.container {
		padding: 0;
	}
}
.xf-content-height {
	margin: 0 !important;
	@media all and (max-width: @bp-med) {
		margin: 0 !important;
	}
}
/* default breakpoint */
.aem-Grid {
	.generate-grid(default, @max_col);
	width: auto;
}

/* phone breakpoint */
@media (max-width: 768px) {
	.aem-Grid {
		.generate-grid(phone, @max_col);
	}
}

/* tablet breakpoint */
@media (min-width: 769px) and (max-width: 1200px) {
	.aem-Grid {
		.generate-grid(tablet, @max_col);
	}
}

/* column padding */
.aem-GridColumn {
	padding: 0 0px;
	@media all and (min-width: @bp-med) {
		padding: 0 15px 0 0;
	}
}

.aem-GridColumn.aem-GridColumn--default--12 {
	padding-left: 0;
	padding-right: 0;
}

.aem-GridColumn.aem-GridColumn--default--12
> .aem-GridColumn.aem-GridColumn--default--12 {
	padding-left: 0;
	padding-right: 0;
}
