.breadcrumb__content {
	background-color: @color-green;
}
.breadcrumb__list {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 0.5rem 0;
	.breadcrumb__link--previousLink {
		text-decoration: underline;
		color: @color-black;
		display: inline-block;
	}
	.breadcrumb__item--left-arrow,
	.breadcrumb__item--right-arrow {
		color: @color-black;
		padding: 0 0.5rem;
	}
	.breadcrumb__item--left-arrow {
		display: inline-block;
		@media all and (min-width: @bp-med) {
			display: none;
		}
	}
	.breadcrumb__item--right-arrow {
		display: none;
		@media all and (min-width: @bp-med) {
			display: inline-block;
		}
	}
	.breadcrumb__item {
		display: none;
		font-size: 0.75rem;
		@media all and (min-width: @bp-med) {
			display: inline-block;
			font-size: 0.875rem;
		}
	}
	.breadcrumb__link--immePreviousLink {
		display: inline-block;
		color: @color-black;
	}
	.breadcrumb__link--currentLink {
		color: @color-black;
	}
}
