.image-list__event {
	background-color: @color-white;

	border-radius: 5px;

	margin-bottom: @gutter-double;
	padding-bottom: @gutter-double;

	p {
		padding: @gutter;
		color: @color-black;
	}

	img {
		width: 100%;
	}

	a {
		&.image-list-event__link {
			margin-left: @gutter;
			text-decoration: underline;
		}
	}
	.banner--button {
		width: 280px;
		button {
			text-align: left;
		}
	}
	button {
		margin-left: @gutter;
		font-size: @font-size-med-3;
		height: 70px;
		min-height: 70px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		width: 90%;
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			width: 90%;
		}
		@media only screen and (min-width: @bp-large) {
			width: 351px;
		}
		span {
			color: @color-dark-blue;
			background: @color-white;
			border-radius: 2px;
			padding: 4px 10px;
		}
	}
}
