/*------------------------------------*\
    #GLOBAL CLASSES
\*------------------------------------*/

/**
 * Clearfix
 */
.cf {
	*zoom: 1;
}

/**
 * Clearfix pseudo-elements
 */
.cf:before,
.cf:after {
	content: ' '; /* 1 */
	display: table; /* 2 */
}

.cf:after {
	clear: both;
}

/**
 * Completely remove from the flow and screen readers.
 */
.is-hidden {
	display: none !important;
	visibility: hidden !important;
}

/**
 * Cursor Text
 */
.cursor--text {
	button,
	a {
		cursor: text;
	}
}

/**
 * Completely remove from the flow but leave available to screen readers.
 */
.is-vishidden {
	position: absolute !important;
	overflow: hidden;
	width: 1px;
	height: 1px;
	padding: 0;
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
}
.b-skip-navigation {
	background: @color-dark-blue;
	a {
		color: @color-white;
		height: 1px;
		left: -10000px;
		overflow: hidden;
		position: absolute;
		top: auto;
		width: 1px;

		&:focus,
		&:visited {
			color: @color-white;
			height: auto;
			position: static;
			width: auto;
		}
	}
}

/**
 * Making background image position scroll
 */
.bg__img--attachment-scroll {
	background-attachment: scroll !important;
}

/**
 * Text align right
 */
.align-right {
	text-align: right;
}

/**
 * Text align center
 */
.align-center {
	text-align: center;
}

/**
 * Text Align left
 */
.align-left {
	text-align: left !important;
}

/**
 * Block level element
 */
.el--block {
	display: block;
}

/**
 * Inlin-Block level element
 */
.el--inline-block {
	display: inline-block;
}

/**
 * Inline level element
 */
.el--inline {
	display: inline;
}

/**
 * Hide on small screens
 */
.hide-small {
	@media all and (max-width: 767px) {
		display: none;
	}
}

/**
 * Hide on medium screens
 */
.hide-med {
	@media all and (min-width: @bp-med) and (max-width: @bp-large) {
		display: none;
	}
}

/**
 * Hide on large screens
 */
.hide-large {
	@media all and (min-width: @bp-large) {
		display: none;
	}
}

/**
 * Show valid text color
 */
.valid {
	color: @color-utility-positive;
}

/**
 * Show error text color
 */
.error {
	color: @color-utility-negative;
	font-size: inherit;
}

/**
 * Show warning text color
 */
.warning {
	color: @color-utility-caution;
}

/**
 * Show informational text color
 */
.information {
	color: @color-utility-neutral;
}

/**
 * Change text to uppercase
 */
.text--uppercase {
	text-transform: uppercase;
}
.text--underline {
	text-decoration: underline;
}

/**
 * Change font color
 */
.color--white {
	color: @color-white !important;
}
.color--black {
	color: @color-black !important;
}
.color--dark-blue {
	color: @color-dark-blue !important;
}
.color--red {
	color: @color-dark-red;
}
.color--label {
	color: #950000;
}

