}
.icon__bg--dark-blue {
	&:hover {
		.fa-caret-right {
			color: @color-dark-blue !important;
		}
	}
	.list__view--icon {
		background: @color-dark-blue;
	}
}
.icon__bg--light-blue {
	&:hover {
		.fa-caret-right {
			color: @color-blue-lighter !important;
		}
	}
	.list__view--icon {
		background: @color-blue-lighter;
	}
}
.icon__bg--lighter-blue {
	&:hover {
		.fa-caret-right {
			color: #6cbee5 !important;
		}
	}
	.list__view--icon {
