/**
 * Change Image Width and Height
 */
.img--small {
	width: auto;
	height: 100%;
}

/**
 * Padding's and margin's
 */
.gutter {
	padding: 0 !important;
}
.gutterAll15 {
	padding: @gutter;
}
.gutterLR15 {
	padding: 0 @gutter !important;
}
.gutterLR30 {
	padding: 0 @gutter-double !important;
}
.offset {
	margin: 0 !important;
}
.offsetL15 {
	margin-left: @gutter;
}
.offsetR15 {
	margin-right: @gutter;
}
.offsetB30 {
	margin-bottom: @gutter * 2;
}
.offsetB40 {
	margin-bottom: 40px;
}
.offsetB15 {
	margin-bottom: @gutter;
}
.offsetT30 {
	margin-top: @gutter * 2;
}
.offsetT15 {
	margin-top: @gutter;
}
.offsetTB30 {
	margin: @gutter * 2;
}

/**
 * Defining position's of an element
 */

.p--relative {
	position: relative;
}
.p--absolute {
	position: absolute;
}

.icon--border {
	&:before {
		border: 2px solid white;
		padding: 2px 6px;
		border-radius: 2px;
	}
}
.icon--now {
	&:before {
		color: white;
		content: 'Now';
		font-size: 12px;
		font-style: italic;
		font-weight: @font-weight-bold;
		vertical-align: middle;
	}
}
.flex--center {
	display: flex;
	align-items: center;
	span {
		margin-right: @gutter;
		color: @color-gray-35;
	}
}
