.table__content {
	margin-top: @gutter-double;

	.table__heading {
		background: @color-dark-blue;
		color: @color-white;
		border-top-left-radius: @border-radius;
		border-top-right-radius: @border-radius;
		padding: @gutter;
		text-align: center;
		p {
			color: inherit;
		}
	}
}
table {
	width: 100%;
	max-width: 100%;
	margin-bottom: @gutter;
	border-collapse: collapse;
	border-spacing: 0;
	background-color: transparent;

	th {
		background: @color-dark-blue;
		color: @color-white;
		cursor: pointer;
	}
	td,
	th {
		font-size: @font-size-small-3;
		font-weight: @font-weight-bold;
		padding: @gutter;
	}
	td:first-child {
		text-align: center;
	}
}
.dataTables_filter {
	background: @color-dark-blue;
	color: @color-white;
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
	padding: 0.9375rem 0.9375rem 0 0.9375rem;
	label {
		margin-bottom: 0 !important;
		font-weight: normal;
		position: relative;
		&:after {
			display: inline-block;
			font: normal normal normal 14px / 1 FontAwesome;
			font-size: inherit;
			text-rendering: auto;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			content: '\f002';
			position: absolute;
			right: 2%;
			top: 30%;
			color: grey;
		}
		input {
			margin-top: 10px;
			padding-right: @gutter-double;
		}
	}
}
.dataTable {
	border-color: transparent;
	caption {
		background: @color-dark-blue;
		color: @color-white;
		padding: 0.9375rem;
		text-align: center;
		font-size: 2.25rem;
		font-weight: 300;
	}
	tr td {
		border-left: 1px solid @color-white;
		text-align: center;
		font-weight: normal;
		border-right-color: transparent;
		border-bottom-color: transparent;
		border-top-color: transparent;
	}
	tr td:first-child {
		border-left: none;
		text-align: left;
	}
	tr th {
		text-align: left;
		padding: 50px 2.175rem;
		width: 20% !important;
		font-weight: normal;
		border-color: transparent;
	}

	tr th:first-child {
		text-align: left;
	}
}
table > tbody > tr:nth-of-type(odd) {
	background-color: #edf0fd;
}
table > tbody > tr:nth-of-type(even) {
	background-color: @color-white;
}
.dataTables_wrapper {
	margin: @gutter 0 @gutter-double 0;
	overflow-x: auto;
	/* width */
	&::-webkit-scrollbar {
		width: 2px;
	}
	/* Track */
	&::-webkit-scrollbar-track {
		background: #f1f1f1;
	}

	/* Handle */
	&::-webkit-scrollbar-thumb {
		background: #888;
	}

	/* Handle on hover */
	&::-webkit-scrollbar-thumb:hover {
		background: #555;
	}
}

.dataTables_info {
	display: block;
	margin-bottom: @gutter;
	font-size: 1rem;
	@media only screen and (min-width: @bp-med) {
		display: inline-block;
	}
	@media only screen and (min-width: @bp-large) {
		display: inline-block;
	}
}
.dataTables_paginate {
	font-size: 1rem;
	display: flex;
	justify-content: space-between;
	margin-bottom: @gutter;
	@media only screen and (min-width: @bp-med) {
		display: inline-block;
		float: right;
	}
	@media only screen and (min-width: @bp-large) {
		display: inline-block;
		float: right;
	}
}
.paginate_button {
	color: @color-dark-blue;
	margin-right: 10px;
	@media only screen and (min-width: @bp-med) {
		margin-right: 10px;
	}
	@media only screen and (min-width: @bp-large) {
		margin-left: 10px;
	}
	cursor: pointer;
	&.disabled {
		color: @color-black;
		cursor: not-allowed;
		&:hover {
			text-decoration: none;
		}
	}
	&.current {
		color: @color-dark-blue;
		font-weight: @font-weight-bold;
	}
}

.easyPaginateNav {
	text-align: center;
	margin: @gutter 0;
	button {
		color: @color-black;
		background: transparent;
		box-shadow: none;
		padding: 0;
		font-size: inherit;
		&:disabled {
			color: gray;
			cursor: text;
		}
	}
	a {
		margin: 0 10px;
		color: @color-black;
		font-size: inherit;
		&.current {
			font-weight: @font-weight-bold;
		}
	}
}

table.dataTable thead .sorting,
table.dataTable thead .sorting_asc,
table.dataTable thead .sorting_desc,
table.dataTable thead .sorting_asc_disabled,
table.dataTable thead .sorting_desc_disabled {
	cursor: pointer;
	*cursor: hand;
	background-repeat: no-repeat;
	background-position: 13px center;
}
table.dataTable thead .sorting {
	background-image: url('../../../../assets/images/datatables/sort_both.png');
}
table.dataTable thead .sorting_asc {
	background-image: url('../../../../assets/images/datatables/sort_asc.png');
}
table.dataTable thead .sorting_desc {
	background-image: url('../../../../assets/images/datatables/sort_desc.png');
}
table.dataTable thead .sorting_asc_disabled {
	background-image: url('../../../../assets/images/datatables/sort_asc_disabled.png');
}
table.dataTable thead .sorting_desc_disabled {
	background-image: url('../../../../assets/images/datatables/sort_desc_disabled.png');
}
