.news__list {
	display: flex;
	.slick-track {
		display: flex !important;
		.slick-slide {
			height: auto !important;
			margin: @gutter;
			border-top-left-radius: @border-radius;
			border-top-right-radius: @border-radius;
			border-bottom-right-radius: @border-radius;
			border-bottom-left-radius: @border-radius;
			background: @color-white;
			.news__item {
				margin: 0 !important;
			}
		}
	}
}

.news__content-carousel {
	border-top: @border-thicker solid #fbb021;
	border-bottom: @border-thicker solid #fbb021;
	background-color: @color-light-green;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	padding: @gutter 0;
	.news__img {
		img {
			height: 135px;
			width: 100%;
		}
	}
}
.news__heading {
	margin: @gutter 0;
	font-weight: 300;
	color: #062f43;
	a {
		color: @color-white;
	}
}

.news__view-all {
	margin: 2.9375rem 0 @gutter 0;
	@media all and (min-width: @bp-med) {
		margin: 2.9375rem 0 @gutter 0;
	}
	@media all and (min-width: @bp-large) {
		margin: 0.9375rem 0 @gutter 0;
	}
	a {
		&:hover {
			text-decoration: underline;
		}
	}
}
.news__detail {
	padding: @gutter;
	min-height: 150px;
	margin-top: -4px;
	p {
		color: @color-black;
		font-weight: @font-weight-bold;
		a {
			display: block;
			font-weight: normal;
			&:hover {
				text-decoration: underline;
			}
		}
	}
}
.news__item {
	margin: 0 @gutter;
	margin-bottom: @gutter;
	flex: 1;
	background: @color-white;
	border-top-left-radius: @border-radius;
	border-top-right-radius: @border-radius;
	border-bottom-right-radius: @border-radius;
	border-bottom-left-radius: @border-radius;
}
