/*------------------------------------*\
    #TABLE
\*------------------------------------*/

/**
 * Table base styles
 */
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: 100%;
}

/**
 * Table header base styles
 */
th {
	text-align: left;
	padding: 0.2em;
}

/**
 * Table cell base styles
 */
td {
	padding: 0.2em;
}
