.events__list,
.image-list__event {
	margin: @gutter-double @gutter;
	@media screen and (min-width: @bp-med) {
		margin: @gutter-double 0;
	}
}
/*----- AEM Specific 2 column layout Events around community -----*/
.community-events {
	.events__list {
		padding: 0 @gutter;
	}
}
/*----- End of AEM Specific 2 column layout Events around community -----*/
.container.responsivegrid.aem-GridColumn {
	width: 100%;
	max-width: 100%;
}
.events__content {
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	padding: @gutter-double 0;

	h2 {
		text-align: center;
		color: @color-white;
		font-weight: @font-weight-regular;
		a {
			color: @color-white;
		}
	}

	.events__wrapper {
		@media screen and (min-width: @bp-med) {
			display: flex;
		}

		.image-list__event {
			@media screen and (min-width: @bp-med) {
				flex: 2;
				margin-right: @gutter-double;
				margin-bottom: 0;
			}
		}

		.events__list {
			@media screen and (min-width: @bp-med) {
				flex: 2;
			}

			li {
				margin-bottom: @gutter;
				color: @color-black;
				display: none;

				p {
					color: @color-black;
				}

				&:nth-child(-n + 3) {
					display: block;
				}

				@media screen and (min-width: @bp-med) {
					display: block;
				}
			}
		}
	}
	.events__view-all {
		display: block;
		text-align: center;
		@media screen and (min-width: @bp-med) {
			text-align: left;
		}
	}
}
