@import './language-selector.less';

.header__content {
	background-color: @color-dark-blue;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	-webkit-justify-content: space-between;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-align-items: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: @gutter 0;
	.container {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		-ms-flex-align: center;
		align-items: center;
		-ms-flex-pack: justify;
		justify-content: space-between;
	}
	.header__logo {
		position: relative;
		width: 167px;
		@media only screen and (min-width: @bp-med) {
			width: 216px;
		}
		.header__logo--text {
			position: absolute;
			font-size: 10px;
			top: -5px;
			left: 40px;
			color: @color-white;
		}
		img {
			width: 100%;
		}
	}
	.navbar__toggle {
		color: @color-white;
		padding: 0;
		border: 0;
		display: flex;
		flex-direction: row;
		align-items: center;
		cursor: pointer;
		box-shadow: none;

		.navbar__title {
			font-size: @font-size-med-3;
			vertical-align: super;
			padding-right: @gutter;
		}
		.navbar__toggle-icon {
			font-size: @font-size-large-2;
		}
		@media all and (min-width: @bp-large) {
			display: none;
		}
	}
	.navbar__close {
		display: flex;
		font-size: @font-size-med-3;
		color: @color-white;
		margin-bottom: 1em;
		align-items: center;
		justify-content: space-between;
		.navbar__close-icon {
			font-size: @font-size-large-2;
		}
		@media all and (min-width: @bp-large) {
			display: none;
		}
	}
	.navbar {
		display: none;
		height: 100%;
		width: 268px;
		position: fixed;
		z-index: 3;
		top: 0;
		right: 0;
		background-color: @color-light-blue;
		overflow-x: hidden;
		transition: 0.5s;
		padding: @gutter-double @gutter;
		@media all and (min-width: @bp-large) {
			display: block;
			position: relative;
			background-color: transparent;
			padding: 0;
			width: auto;
			overflow-x: unset;
			overflow: visible;
		}
	}
	.header__list {
		display: block;
		@media all and (min-width: @bp-large) {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
		}
		.header__item {
			display: flex;
			align-items: center;
			border-bottom: 1px solid#ffffff80;
			@media all and (min-width: @bp-large) {
				border-bottom: none;
			}
			.header__link {
				padding: @gutter 0;
				font-size: @font-size-med-3;
				color: @color-white;
				&:hover {
					color: @color-white;
					text-decoration: none;
				}
				@media all and (min-width: @bp-large) {
					font-size: @font-size-small-3;
					padding: 0 0 0 @gutter;
				}
			}
			&.header__search {
				position: relative;
				border: none;
				margin: @gutter-double 0;
				border-bottom: 0rem;
				@media all and (min-width: @bp-large) {
					width: 236px;
					margin: 0 0 0 20px;
				}
				input {
					padding-right: @gutter-double;
					width: 236px;
					-webkit-transition: width 0.2s ease-in-out;
					transition: width 0.2s ease-in-out;
					@media all and (min-width: @bp-large) {
						position: absolute;
						right: 0;
						top: 4px;
						&:focus {
							width: 460px;
							position: absolute;
							right: 0;
						}
					}
				}
				.search__icon--header-position {
					position: absolute;
					top: 32%;
					right: 5%;
					color: @color-gray-35;
				}
			}
		}
	}

	.header__nav--is-active {
		display: block;
	}
	&.header__search--hide {
		.header__search {
			display: none;
		}
	}
}
