.purpose__banner {
	background: #314c9d;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	.container {
		display: flex;
		justify-content: flex-start;
		@media only screen and (min-width: @bp-med) {
			justify-content: flex-end;
		}
	}
}
.purpose__banner-content {
	padding: @gutter-double;
	width: 100%;
	display: flex;

	flex-direction: column;

	@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
		flex-direction: column;
		width: 50%;
	}
	@media all and (min-width: @bp-large) {
		flex-direction: column;
		width: 50%;
	}
	h2 {
		color: @color-white;
		a {
			color: @color-white;
		}
	}
	p {
		padding: @gutter 0 @gutter-double 0;
		color: @color-white;
	}
	.btn {
		width: 100%;
		height: 70px;
		margin: @gutter-double 0;
		text-align: left;
		border: 1px solid @color-white;
		border-radius: 4px;
		background: transparent;
		color: @color-white;
		font-size: 1.25rem;
		box-shadow: none;
		&:hover {
			background: transparent;
			border: 1px solid rgba(255, 255, 255, 0.5);
		}
		@media all and (min-width: @bp-med) {
			width: 350px;
		}
		span {
			margin-right: @gutter-double;
			vertical-align: middle;
			&.fa-heart {
				color: @color-red;
			}
		}
	}
}
