.news-article {
	&__btn {
		flex: 20% !important;
		justify-content: flex-start;
		display: flex;
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			justify-content: flex-start;
		}
		@media all and (min-width: @bp-large) {
			justify-content: flex-end;
		}

		a {
			margin-right: @gutter-double;
			margin-left: 0;
			text-align: center;
			@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
				text-align: center;
				margin-right: @gutter-double;
				margin-left: 0;
			}
			@media all and (min-width: @bp-large) {
				text-align: left;
				margin-left: @gutter-double;
				margin-right: 0;
			}
			&:hover,
			&:focus,
			&:visited {
				text-decoration: none;
			}
		}
		.fa {
			color: @color-dark-blue;
			display: block;
			text-align: center;
		}
		em {
			font-size: 12px;
			font-style: normal;
		}
	}
	&__title {
		padding: 45px 0 @gutter 0;
		display: block;
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			display: block;
		}
		@media all and (min-width: @bp-large) {
			display: flex;
		}
		p {
			font-size: 3rem;
			line-height: inherit;
			@media only screen and (min-width: @bp-med) {
				font-size: 2.625rem;
			}
		}
		div {
			flex: 80%;
		}
	}
	&__text {
		display: block;
		overflow: hidden;
		padding: @gutter 0;
		img {
			&.img--left {
				float: left;
				margin: 0 @gutter-double @gutter 0;
			}
			&.img--right {
				float: right;
				margin: 0 @gutter @gutter-double;
			}
			&.img--center {
				float: none;
				display: block;
				margin: 0 auto;
			}
		}
		p {
			display: block;
		}
	}
}
