/*------------------------------------*\
    #HEADINGS
\*------------------------------------*/

/**
 * Change font weight
 */
.font--bold {
	font-weight: @font-weight-bold !important;
}
.font--regular {
	font-weight: @font-weight-regular;
}

/**
 * Heading 1, equivalent of h1
 */
.heading--xlarge,
h1 {
	font-size: @font-size-med;
	font-weight: @font-weight-regular;
	@media all and (min-width: @bp-med) {
		font-size: @font-size-large;
	}
}

/**
 * Heading 2, equivalent of h2
 */
.heading--large,
h2 {
	font-size: @font-size-med-2;
	font-weight: @font-weight-regular;
	@media all and (min-width: @bp-med) {
		font-size: @font-size-med;
		font-weight: @font-weight-regular;
	}
}

/**
 * Heading 3, equivalent of h3
 */
.heading--medium,
h3 {
	font-size: @font-size-med-4;
	font-weight: @font-weight-regular;
	@media all and (min-width: @bp-med) {
		font-size: @font-size-med-2;
		font-weight: @font-weight-regular;
	}
}

/**
 * Heading 4, equivalent of h4
 */
.heading--small,
h4 {
	font-size: @font-size-small-2;
	font-weight: @font-weight-regular;
	@media all and (min-width: @bp-med) {
		font-size: @font-size-small;
		font-weight: @font-weight-regular;
	}
}

/**
 * Heading 5, equivalent of h5
 */
.heading--xsmall,
h5 {
	font-size: @font-size-small-3;
	font-weight: @font-weight-regular;
	@media all and (min-width: @bp-med) {
		font-size: @font-size-small-2;
		font-weight: @font-weight-regular;
	}
}

/**
 * Heading 6, equivalent of h6
 */
.heading--xxsmall,
h6 {
	font-size: @font-size-small-4;
	font-weight: @font-weight-regular;
	@media all and (min-width: @bp-med) {
		font-size: @font-size-small-3;
		font-weight: @font-weight-regular;
	}
}
