@import './banner-text';
.banner__content,
.program__banner,
.category__banner,
.aboutus__banner {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	text-align: center;
	padding: 1.25rem 0 1.25rem 0;
	p {
		margin-bottom: @gutter;
		margin-top: @gutter;
		max-width: 1000px;
	}
	.banner__title {
		margin-bottom: 0;
		p {
			font-size: inherit;
			line-height: inherit;
		}
	}
}

.aboutus__banner {
	p {
		color: @color-white;
		max-width: 1000px;
	}
	ul {
		margin: @gutter-double 0 @gutter 0;
	}
}
.category__banner-list {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	text-align: left;
	li {
		width: 100%;
		margin: 0 1.875rem 0.9375rem 0;
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			width: 50%;
		}
		@media all and (min-width: @bp-large) {
			width: 30%;
		}
	}
}
.category__banner {
	p {
		color: @color-white;
		max-width: 1000px;
	}
	ul {
		li {
			margin-bottom: @gutter;
			a {
				color: @color-white;
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}

.banner__search {
	position: relative;
	text-align: center;
}
.search__results {
	margin-bottom: @gutter-double;
	.rte__content {
		max-width: 1000px;
	}
}
.load__more-search-results {
	text-align: center;
	margin: 60px @gutter;
	button {
		border-radius: 4px;
		width: 280px;
	}
}
.search__content {
	@media all and (max-width: @bp-med) {
		margin: @gutter;
	}
}

.banner__search-input {
	padding: @gutter 3.25rem @gutter @gutter;
	font-size: @font-size-med-3;
	line-height: 1.4375rem;
	border-radius: @border-radius;
	border: 1px solid #808284;
	margin-bottom: @gutter;
	width: 100%;
	@media only screen and (min-width: @bp-med) {
		width: 68.5%;
		margin: 0 auto @gutter-double;
	}
	@media all and (min-width: @bp-large) {
		margin: @gutter-double;
	}
}
.search__icon--position {
	position: absolute;
	top: 25%;
	right: 5%;
	color: @color-gray-35;
	font-size: 1.35rem;
	@media all and (min-width: @bp-med) {
		right: 17.5%;
		top: 20%;
	}
	@media all and (min-width: @bp-large) {
		top: 40%;
	}
}
.self-service__list {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	text-align: left;
	margin-top: 40px;
	justify-content: space-between;
}
.self-service__item {
	width: 100%;
	display: flex;
	list-style: none;
	min-height: 70px;
	height: 70px;
	background-color: @color-darker-blue;
	border-radius: @border-radius;
	margin: 0 0 10px 0;
	-webkit-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
	-moz-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
	box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
	&:hover {
		background: @color-dark-blue-hover;
	}
	@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
		width: 48%;
		margin: 0 0 @gutter 0;
	}
	@media only screen and (min-width: @bp-large) {
		width: 32%;
		margin: 0 0 @gutter 0;
	}
	.self-service__link {
		display: flex;
		align-items: center;
		font-size: @font-size-med-3;
		padding: 0 @gutter;
		vertical-align: middle;
		width: 100%;
		color: @color-white;
		&:hover {
			text-decoration: none;
		}
		.self-service__img {
			font-size: @font-size-med-1;
			margin-right: @gutter;
			min-width: 45px;
		}
	}
}
.banner__heading--color {
	color: @color-darker-blue;
	font-weight: @font-weight-bold;
}
.self-service {
	&__item--red {
		background-color: @color-dark-red;
		border: 1px solid @color-white;
		border-radius: 0;
		.self-service__link {
			padding-left: 0;
			font-size: @font-size-small-3;
			@media only screen and (min-width: @bp-med) {
				font-size: @font-size-small-2;
			}
			img {
				margin-right: @gutter;
				min-width: 45px;
			}
		}
		&:hover {
			background: @color-dark-red-hover;
		}
	}
}
.self-service__item--dark-blue {
	background-color: @color-dark-blue;
}
.self-service__item--grey {
	background: @colo-grey;
}
.self-service__list--Variation-2 {
	margin-top: @gutter;
	.self-service__item {
		min-height: 50px;
		height: 50px;
		padding: @gutter @gutter-double;
		.self-service__link {
			font-size: @font-size-small-3;
			padding: 0;
			.self-service__img {
				font-size: @font-size-small;
				min-width: 35px;
			}
		}
	}
}
.program__para--text {
	color: @color-white;
	margin: @gutter-double 0;
}
.program__banner,
.aboutus__banner,
.category__banner {
	margin-bottom: @gutter-double;
	p,
	.banner__title {
		max-width: 1000px;
	}
}
