@baseClass: .accordion-card;

@{baseClass} {
	margin: 0 0 @gutter 0;

	.faq-accordion__content {
		display: none;
	}
	&__header {
		border-bottom: 2px solid @color-dark-blue;
		display: flex;
		flex-grow: 1;
		min-height: 45px;
		height: 45px;
		padding-bottom: 7px;
		cursor: pointer;

		&--title {
			display: flex;
			align-items: center;
			vertical-align: middle;
			width: 100%;
			h3,
			.heading--medium {
				font: @font-weight-bold @font-size-small-3 @font;
				@media only screen and (min-width: @bp-med) {
					font: @font-weight-bold @font-size-small-2 @font;
				}
			}
		}

		&--icon {
			button {
				background: none;
				border: none;
				padding: 0;
				padding-left: 5px;
				box-shadow: none;
				margin: 0px;

				i {
					color: @color-dark-blue;
					font-size: @font-size-large-2;
				}
			}
			@media only screen and (min-width: @bp-med) {
				.fa-plus-square {
					display: none;
				}
			}
		}
		&.active {
			.fa-plus-square:before {
				content: '\F146';
			}
		}
	}

	&__content {
		height: auto;
		opacity: 1;
		max-height: 0;
		overflow: hidden;
		transition: max-height 0.2s ease-out;
		&.show {
			max-height: 100%;
		}
		@media only screen and (min-width: @bp-med) and (max-width: @bp-med-max) {
			max-height: 100%;
		}
		@media only screen and (min-width: @bp-large) {
			max-height: 100%;
		}
	}

	&__image {
		padding-top: @gutter;

		img {
			width: 100%;
		}
	}

	&__description {
		padding-top: 5px;

		p {
			color: black;
			margin: 0;
		}

		&--para {
			height: 90px;
			overflow: hidden;
		}

		&--para-full {
			height: auto;
		}
	}

	&--hidden,
	&__header--hidden,
	&__image--hidden,
	&__description--hidden,
	&__showmore--hidden {
		display: none;
	}
}
