/*------------------------------------*\
    #BODY
\*------------------------------------*/

/**
 * Body base styles
 */
body {
	background: @color-white;
	color: @color-black;
	font: @font-weight-regular 1rem/1.2 @font;
	-webkit-text-size-adjust: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	overflow-x: hidden;
	@media all and (min-width: @bp-med) {
		font: @font-weight-regular 1.125rem/1.2 @font;
	}
}
