.cookies__content {
	background: @color-dark-blue;
	padding: @gutter-double 0;
	color: @color-white;
	.cookies__text {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: column;
		@media only screen and (min-width: @bp-med) {
			flex-direction: row;
		}
		p {
			color: inherit;
			margin: @gutter @gutter-double 0 0;
			max-width: 1000px;
			a {
				color: inherit;
			}
		}
		button {
			background: @color-blue;
		}
	}
}
