/*------------------------------------*\
    #MEDIA ELEMENTS
\*------------------------------------*/

/**
 * Make media elements responsive
 */
img,
video,
object {
	max-width: 100%;
	height: auto;
}

/**
 * Iframe base styles
 */
iframe {
	margin-bottom: @gutter;
}

/**
 * Figure base styles
 */
figure {
	margin-bottom: @gutter;

	/**
	 * Figure image base styles
	 */
	img {
		margin-bottom: @gutter;
	}
}

/**
 * Figure caption base styles
 */
figcaption {
	font-style: italic;
}
